<template>
<div class="scratching">
    <canvas id="scratch" width="640" height="880" :style="{ pointerEvents: isMarkedForDeath ? 'none' : 'auto'}" />
</div>
</template>

<script lang="ts">
import {
    GameLoop,
    Sprite,
    getContext,
    getPointer,
    init,
    initPointer,
    pointerPressed,
    track,
    untrack
} from 'kontra'
import { defineComponent } from 'vue'

import type { TriviaDeath2 } from '../TriviaDeath2'

export default defineComponent({

    props: {
        player: {
            type: Object as Prop<TriviaDeath2.Scratching.Entity>,
            required: true
        }
    },

    data() {
        const failImage = new Image()
        failImage.src = new URL('../assets/scratch/skull.png', import.meta.url).href

        const successImage = new Image()
        successImage.src = new URL('../assets/scratch/dollar.png', import.meta.url).href

        return {
            // Kontra classes
            scratchBox: {
                height: 126,
                width: 126,
                x: 0,
                y: 0,

                color: 'gray',
                imageThickness: 0,
                lines: null as [{ x: number, y: number }][] | null,
                thickness: 10,

                hiddenCanvas: document.createElement('canvas'),

                failImage,
                successImage,

                totalDistance: 0,
                revealDistance: 400,

                index: -1,

                isDrawing: false,
                isRevealed: false,

                onOver() {
                    if (!this.isDrawing) return

                    if (this.lines) {
                        const line = this.lines[this.lines.length - 1]
                        if (!line) return

                        const prevPoint = line[line.length - 1]

                        // FIXME: point.x and point.y can exceed the width and
                        // height of the scratchBox sprite because Kontra's
                        // default pointer has a radius of 5 when checking for
                        // collision. Not sure how the original avoids this.
                        const pointer = getPointer()
                        const point = { x: pointer.x - this.x, y: pointer.y - this.y }
                        line.push(point)

                        const vec = { x: point.x - prevPoint.x, y: point.y - prevPoint.y }
                        this.totalDistance += Math.sqrt(vec.x * vec.x + vec.y * vec.y)
                        if (!this.isRevealed && this.totalDistance > this.revealDistance) {
                            // Reveal the symbol underneath
                            this.onReveal(this.index)
                            this.isRevealed = true
                        }
                    }
                },

                onReveal(index: number) {
                    // intentional no-op, will be replaced after canvas and
                    // Sprite initialization
                },

                render() {
                    if (!this.hiddenCanvas.width) return

                    const ctx = this.hiddenCanvas.getContext('2d')
                    if (!ctx) return

                    ctx.clearRect(0, 0, this.hiddenCanvas.width, this.hiddenCanvas.height)
                    ctx.save()

                    if (this.lines) {
                        // Image lines
                        if (this.isRevealed) {
                            ctx.globalCompositeOperation = 'source-over'
                            // Draw the lines with imageThickness
                            ctx.strokeStyle = '#bbbbbb'
                            ctx.lineWidth = this.imageThickness

                            this.lines.forEach((line) => {
                                ctx.beginPath()
                                ctx.moveTo(line[0].x, line[0].y)
                                line.forEach((point) => {
                                    ctx.lineTo(point.x, point.y)
                                })
                                ctx.stroke()
                            })
                            // only keep where both have color
                            ctx.globalCompositeOperation = 'source-in'
                            if (this.color === 'red' && this.failImage) {
                                ctx.drawImage(this.failImage, 0, 0, this.width, this.height)
                            } else if (this.color === 'green' && this.successImage) {
                                ctx.drawImage(this.successImage, 0, 0, this.width, this.height)
                            }
                        }
                        // Lines
                        ctx.globalCompositeOperation = 'destination-over'
                        ctx.strokeStyle = '#bbbbbb'
                        ctx.lineWidth = this.thickness
                        this.lines.forEach((line) => {
                            ctx.beginPath()
                            ctx.moveTo(line[0].x, line[0].y)
                            line.forEach((point) => {
                                ctx.lineTo(point.x, point.y)
                            })
                            ctx.stroke()
                        })
                    }

                    ctx.restore()
                    getContext().drawImage(this.hiddenCanvas, 0, 0)
                },

                update() {
                    if (!this.lines) this.lines = []
                    if (!this.hiddenCanvas.width) {
                        this.hiddenCanvas.width = this.width
                        this.hiddenCanvas.height = this.height
                    }

                    if (this.isRevealed) {
                        this.imageThickness = Math.min(this.imageThickness + 10 / 60, this.width * 2)
                    }

                    this.isDrawing = pointerPressed('left')
                    if (this.isDrawing) {
                        // Start a line
                        const pointer = getPointer()
                        this.lines.push([{ x: pointer.x - this.x, y: pointer.y - this.y }])
                    }
                }
            },

            // Kontra data
            gameLoop: null as GameLoop | null,
            sprites: [] as Sprite[]
        }
    },

    computed: {
        isMarkedForDeath() {
            for (let i = 0; i < this.player.choices.length; i++) {
                const sprite = this.sprites.find((s) => s.index === i)
                if (sprite?.isRevealed && sprite?.color === 'red') {
                    return true
                }
            }

            return false
        }
    },

    watch: {
        'player.choices': function updateSpriteColors() {
            this.setSpriteColors()
        }
    },

    mounted() {
        init('scratch')
        initPointer()

        this.gameLoop = GameLoop({
            fps: 60,

            update: (dt) => {
                this.sprites.forEach((s) => s.update(dt))
                this.sprites = this.sprites.filter((s) => s.isAlive())
            },

            render: () => {
                this.sprites.forEach((s) => s.render())
            }
        })

        const left = 117
        const top = 372
        const padding = 14

        // rows
        for (let y = 0; y < 3; y++) {
            // columns
            for (let x = 0; x < 3; x++) {
                const s = Sprite(this.scratchBox)
                s.x = left + x * (s.width + padding)
                s.y = top + y * (s.height + padding)
                s.index = 3 * y + x

                s.onReveal = () => {
                    void this.onReveal(s.index as number)
                }

                track(s)
                this.sprites.push(s)
            }
        }

        this.setSpriteColors()

        this.gameLoop.start()
    },

    beforeUnmount() {
        for (let i = 0; i < this.sprites.length; i++) {
            untrack(this.sprites[i])
            this.sprites[i].ttl = 0 // reset
        }
        this.gameLoop?.update(1 / 60)
        this.gameLoop?.stop()
    },

    methods: {
        setSpriteColors() {
            this.player.choices.forEach((choice, choiceIdx) => {
                const sprite = this.sprites.find((s) => s.index === choiceIdx)
                if (sprite) {
                    sprite.color = choice.color
                }
            })
        },

        async onReveal(index: number) {
            try {
                await this.$ecast.updateObject<TriviaDeath2.Scratching.Response>(
                    this.player.responseKey,
                    {
                        action: 'scratch',
                        index
                    }
                )
            } catch (error) {
                this.$handleEcastError(error)
            }
        }
    }
})
</script>

<style lang="scss" scoped>
.scratching {
    display: flex;
    flex-direction: column;
    align-items: center;

    canvas {
        width: clamp(150px, min(100%, 65vh), 640px);
        border: none;
        background-color: transparent;
        background-image: url("../assets/scratch/ticket.png");
        background-repeat: no-repeat;
        background-size: contain;

        .locale-de &,
        .locale-es &,
        .locale-es-XL &,
        .locale-fr &,
        .locale-it & {
            background-image: url("../assets/scratch/ticket-int.png");
        }
    }
}
</style>
