<template>
<div class="doodle">
    <div
        v-pointerbox
        class="stage"
        v-on:pointerbox:start="onPointerBoxStart"
        v-on:pointerbox:move="onPointerBoxMove"
        v-on:pointerbox:end="onPointerBoxEnd"
    >
        <canvas
            ref="canvas"
            v-pointerbox-translate="{
                id: 'doodleCanvas',
                width: pointerBoxWidth,
                height: pointerBoxHeight
            }"
        />
    </div>
    <button
        v-if="!hideUndo"
        v-t="'ACTION.UNDO'"
        class="doodle-action undo"
        :disabled="!canSubmit"
        v-on:click.prevent="onUndo"
    />
    <button
        v-if="!hideSubmit"
        v-t="'ACTION.SUBMIT'"
        class="doodle-action submit"
        :disabled="!canSubmit"
        v-on:click.prevent="onSubmit"
    />
</div>
</template>


<script lang="ts">
import { defineComponent, getCurrentInstance, reactive } from 'vue'
import type { WSClient } from '@jackboxgames/ecast'

import { DoodleCanvas } from '@tv/shared'

import type { CommonEntities, PointerBox } from '@tv/shared'

export default defineComponent({
    props: {
        canvasOptions: Object as Prop<DoodleCanvas.Options>,
        hideSubmit: Boolean,
        hideUndo: Boolean,
        player: Object as Prop<CommonEntities.Doodle>
    },

    data() {
        return {
            canvas: null as DoodleCanvas | null,
            isSubmitting: false,
            isUndoing: false
        }
    },

    computed: {
        canSubmit(): boolean {
            if (this.isSubmitting) return false
            if (this.isUndoing) return false
            if (this.player!.doodle.lines.length <= 0) return false

            return true
        },

        pointerBoxWidth(): number {
            let width = 320
            if (this.player!.doodle.size?.width) {
                width = this.player!.doodle.size.width
            }

            // Scale PointerBox width to match canvas dimension
            return width * (this.canvasOptions?.scale?.width ?? 1)
        },

        pointerBoxHeight(): number {
            let height = 320
            if (this.player!.doodle.size?.height) {
                height = this.player!.doodle.size.height
            }

            // Scale PointerBox height to match canvas dimension
            return height * (this.canvasOptions?.scale?.height ?? 1)
        }
    },

    watch: {
        'canvasOptions': function updateOptions(newOptions: DoodleCanvas.Options) {
            if (!this.canvas) return

            this.canvas.brush = newOptions.brush ?? ''
            this.canvas.color = newOptions.color ?? '#000000'
            this.canvas.layer = newOptions.layer ?? 0
            this.canvas.precision = newOptions.precision ?? 2
            this.canvas.scale = newOptions.scale ?? { width: 1, height: 1 }
            this.canvas.tolerance = newOptions.tolerance ?? 0.5
            this.canvas.weight = newOptions.weight ?? 4
        },

        'player.doodle.key': function resetForNewDoodle() {
            this.createCanvas()
        },

        // Since moving to Vue 3, the deep flag is required for this to trigger
        // after an undo. I'm unsure why this is needed.
        'player.doodle.lines': {
            handler() {
                if (!this.canvas) return
                this.canvas.drawLines()
            },
            deep: true
        }
    },

    beforeMount() {
        const instance = getCurrentInstance()
        if (!instance?.appContext.directives.pointerbox) {
            throw new Error('Doodle.vue relies on PointerBoxPlugin. Please install it inside your main.ts file.')
        }
    },

    mounted() {
        this.createCanvas()
    },

    methods: {
        createCanvas() {
            const canvasEl = this.$refs.canvas as HTMLCanvasElement
            this.canvas = reactive(new DoodleCanvas(
                canvasEl,
                this.player!.doodle,
                this.canvasOptions
            ))
        },

        onPointerBoxStart(event: PointerBox.StaticEvent) {
            if (!this.canvas) return

            const point = {
                x: event.detail.translations.doodleCanvas.x,
                y: event.detail.translations.doodleCanvas.y
            } as WSClient.DoodlePoint

            this.canvas.onStart(point)
        },

        onPointerBoxMove(event: PointerBox.MoveEvent) {
            if (!this.canvas) return

            const point = {
                x: event.detail.translations.doodleCanvas.x,
                y: event.detail.translations.doodleCanvas.y
            } as WSClient.DoodlePoint

            this.canvas.onMove(point)
        },

        async onPointerBoxEnd() {
            if (!this.canvas) return

            const line = this.canvas.onEnd()

            if (line) {
                try {
                    await this.$ecast.strokeDoodle(this.player!.doodle.key, line)
                } catch (err) {
                    this.$handleEcastError(err)
                }
            }
        },

        async onSubmit() {
            this.isSubmitting = true

            try {
                await this.$ecast.lock(this.player!.doodle.key)
            } catch (err) {
                this.isSubmitting = false

                this.$handleEcastError(err)
            }
        },

        async onUndo() {
            if (!this.canvas) return

            this.isUndoing = true

            try {
                await this.$ecast.undoDoodle(this.player!.doodle.key)
            } catch (error) {
                this.$handleEcastError(error)
            } finally {
                this.isUndoing = false
            }
        }
    }
})
</script>
