<template>
<div class="choices">
    <div class="constrain">
        <p v-if="player!.prompt" v-bb="player!.prompt" />
        <button
            v-for="(choice, index) in player!.choices"
            :key="index"
            :class="{ selected: choice.isSelected }"
            :disabled="choice.isDisabled"
            v-on:click.prevent="onChoiceClick(index)"
        >
            {{ choice.text }}
        </button>
    </div>
</div>
</template>


<script lang="ts">
import { defineComponent } from 'vue'
import { CommonEntities } from '@tv/shared'

export default defineComponent({
    props: {
        player: Object as Prop<CommonEntities.Choices>
    },

    methods: {
        onChoiceClick(index: number) {
            const choice = this.player!.choices[index]

            if (choice.send) {
                void this.$ecast.updateObject(this.player!.responseKey, choice.send).catch(this.$handleEcastError)
                return
            }

            const action = this.player!.action ?? 'choice'
            const key = this.player!.key ?? 'value'
            const value = choice.value ?? index
            const data = {
                action,
                [key]: value
            }

            void this.$ecast.updateObject(this.player!.responseKey, data).catch(this.$handleEcastError)
        }
    }
})
</script>
