<template>
<div class="choices">
    <span v-bb="prompt" class="message prompt" />
    <div v-if="hasSubmitted" class="chosen">
        <span v-t="'CHOICES.CHOSEN'" />
        <br>
        <span v-if="selections.length" v-bb="selections" />
        <span v-else v-t="'CHOICES.NONE'" />
    </div>
    <template v-else>
        <div
            :class="
                (player.category && ['fingers', 'keys', 'postGame', 'wires'].indexOf(player.category) === -1)
                    ? 'object-container'
                    : 'choice-container'
            "
        >
            <template v-if="madness === 'Choices' || madness === 'NowThatsWhatICallMadness'">
                <div v-for="index in 10" :key="`madness-${index}`" class="option-container">
                    <button
                        :disabled="!canSubmit"
                        class="action choice"
                        v-on:click="onSelect(-1)"
                    >
                        <span
                            v-if="madness === 'Choices'"
                            v-t="'CHOICES.MADNESS'"
                        />
                        <span
                            v-else-if="madness === 'NowThatsWhatICallMadness'"
                            v-bb="makeLettersDisappear(scramble(String($t('CHOICES.MADNESS'))))"
                        />
                    </button>
                    <img
                        v-if="player.hasSubmit && buttonImages['uncheckedBox']"
                        class="checkbox-image"
                        :src="buttonImages['uncheckedBox'].src"
                        :alt="buttonImages['uncheckedBox'].alt"
                    >
                </div>
            </template>
            <div v-for="(choice, index) in choices" :key="`choice-${index}`" class="option-container">
                <button
                    :disabled="!canSubmit || choice.disabled || choice.isFingerCutOff"
                    class="action choice"
                    :class="generateChoiceClasses(index, choice)"
                    v-on:click="onSelect(index)"
                >
                    <span v-if="showChoiceText" v-bb="choice.text" />
                    <img
                        v-else-if="buttonImages[choice.optionType + choice.objectIndex]"
                        class="button-image"
                        :class="player.category"
                        :src="buttonImages[choice.optionType + choice.objectIndex].src"
                        :alt="buttonImages[choice.optionType + choice.objectIndex].alt"
                    >
                </button>
                <img
                    v-if="player.hasSubmit && buttonImages['checkedBox'] && buttonImages['uncheckedBox']"
                    class="checkbox-image"
                    :src="buttonImages[selected.indexOf(index) !== -1 ? 'checkedBox' : 'uncheckedBox'].src"
                    :alt="buttonImages[selected.indexOf(index) !== -1 ? 'checkedBox' : 'uncheckedBox'].alt"
                >
            </div>
        </div>

        <button
            v-if="player.hasSubmit"
            class="submit"
            v-on:click="onSubmit"
            v-text="$t('ACTION.SUBMIT')"
        />
    </template>
    <!--
        HACK: TMP2's game code doesn't define a post-game interaction. It uses
        Choices to show Same/New Players buttons instead. We must rely on the
        category to determine if this phony choice should show a link to the
        post-game gallery.

        TMP2 POST-GAME CONTROLLER ANALYTICS ARE UNTRUSTWORTHY BECAUSE OF THIS
    -->
    <GalleryLink v-if="player.category === 'postGame'" :artifact="artifact" />
</div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'

import { GalleryLink } from '@tv/vue'
import type { CommonEntities } from '@tv/shared'

import type { TriviaDeath2 } from '../TriviaDeath2'

export default defineComponent({
    components: {
        GalleryLink
    },

    props: {
        artifact: Object as Prop<CommonEntities.Artifact>,
        madness: String,
        player: {
            type: Object as Prop<TriviaDeath2.Choices.Entity>,
            required: true
        }
    },

    bb: {
        disappear: (_, content) => {
            const duration = Math.floor(Math.random() * 5)
            return `<span class="disappear-${duration}">${content}</span>`
        }
    },

    data() {
        return {
            buttonImages: {
