                    src: new URL('../assets/chalices/9.png', import.meta.url).href
                },
                chalice9: {
                    alt: this.$t('ALT.CHALICE_10'),
                    src: new URL('../assets/chalices/10.png', import.meta.url).href
                },
                chalice10: {
                    alt: this.$t('ALT.CHALICE_11'),
                    src: new URL('../assets/chalices/11.png', import.meta.url).href
                },
                chalice11: {
                    alt: this.$t('ALT.CHALICE_12'),
                    src: new URL('../assets/chalices/12.png', import.meta.url).href
                },
                finger0: {
                    alt: this.$t('ALT.FINGER_1'),
                    src: new URL('../assets/fingers/1.png', import.meta.url).href
                },
                finger1: {
                    alt: this.$t('ALT.FINGER_2'),
                    src: new URL('../assets/fingers/2.png', import.meta.url).href
                },
                finger2: {
                    alt: this.$t('ALT.FINGER_3'),
                    src: new URL('../assets/fingers/3.png', import.meta.url).href
                },
                finger3: {
                    alt: this.$t('ALT.FINGER_4'),
                    src: new URL('../assets/fingers/4.png', import.meta.url).href
                },
                gift0: {
                    alt: this.$t('ALT.GIFT_1'),
                    src: new URL('../assets/gifts/1.png', import.meta.url).href
                },
                gift1: {
                    alt: this.$t('ALT.GIFT_2'),
                    src: new URL('../assets/gifts/2.png', import.meta.url).href
                },
                gift2: {
                    alt: this.$t('ALT.GIFT_3'),
                    src: new URL('../assets/gifts/3.png', import.meta.url).href
                },
                gift3: {
                    alt: this.$t('ALT.GIFT_4'),
                    src: new URL('../assets/gifts/4.png', import.meta.url).href
                },
                gift4: {
                    alt: this.$t('ALT.GIFT_5'),
                    src: new URL('../assets/gifts/5.png', import.meta.url).href
                },
                gift5: {
                    alt: this.$t('ALT.GIFT_6'),
                    src: new URL('../assets/gifts/6.png', import.meta.url).href
                },
                gift6: {
                    alt: this.$t('ALT.GIFT_7'),
                    src: new URL('../assets/gifts/7.png', import.meta.url).href
                },
                gift7: {
                    alt: this.$t('ALT.GIFT_8'),
                    src: new URL('../assets/gifts/8.png', import.meta.url).href
                },
                key0: {
                    alt: this.$t('ALT.KEY_1'),
                    src: new URL('../assets/keys/1.png', import.meta.url).href
                },
                key1: {
                    alt: this.$t('ALT.KEY_2'),
                    src: new URL('../assets/keys/2.png', import.meta.url).href
                },
                key2: {
                    alt: this.$t('ALT.KEY_3'),
                    src: new URL('../assets/keys/3.png', import.meta.url).href
                },
                key3: {
                    alt: this.$t('ALT.KEY_4'),
                    src: new URL('../assets/keys/4.png', import.meta.url).href
                },
                key4: {
                    alt: this.$t('ALT.KEY_5'),
                    src: new URL('../assets/keys/5.png', import.meta.url).href
                },
                key5: {
                    alt: this.$t('ALT.KEY_6'),
                    src: new URL('../assets/keys/6.png', import.meta.url).href
                },
                key6: {
                    alt: this.$t('ALT.KEY_7'),
                    src: new URL('../assets/keys/7.png', import.meta.url).href
                },
                key7: {
                    alt: this.$t('ALT.KEY_8'),
                    src: new URL('../assets/keys/8.png', import.meta.url).href
                },
                key8: {
                    alt: this.$t('ALT.KEY_9'),
                    src: new URL('../assets/keys/9.png', import.meta.url).href
                },
                key9: {
                    alt: this.$t('ALT.KEY_10'),
                    src: new URL('../assets/keys/10.png', import.meta.url).href
                },
                key10: {
                    alt: this.$t('ALT.KEY_11'),
                    src: new URL('../assets/keys/11.png', import.meta.url).href
                },
                key11: {
                    alt: this.$t('ALT.KEY_12'),
                    src: new URL('../assets/keys/12.png', import.meta.url).href
                },
                key12: {
                    alt: this.$t('ALT.KEY_13'),
                    src: new URL('../assets/keys/13.png', import.meta.url).href
                },
                key13: {
                    alt: this.$t('ALT.KEY_14'),
                    src: new URL('../assets/keys/14.png', import.meta.url).href
                },
                key14: {
                    alt: this.$t('ALT.KEY_15'),
                    src: new URL('../assets/keys/15.png', import.meta.url).href
                },
                key15: {
                    alt: this.$t('ALT.KEY_16'),
                    src: new URL('../assets/keys/16.png', import.meta.url).href
                },
                key16: {
                    alt: this.$t('ALT.KEY_17'),
                    src: new URL('../assets/keys/17.png', import.meta.url).href
                },
                key17: {
                    alt: this.$t('ALT.KEY_18'),
                    src: new URL('../assets/keys/18.png', import.meta.url).href
                },
                key18: {
                    alt: this.$t('ALT.KEY_19'),
                    src: new URL('../assets/keys/19.png', import.meta.url).href
                },
                key19: {
                    alt: this.$t('ALT.KEY_20'),
                    src: new URL('../assets/keys/20.png', import.meta.url).href
                },
                key20: {
                    alt: this.$t('ALT.KEY_21'),
                    src: new URL('../assets/keys/21.png', import.meta.url).href
                },
                key21: {
                    alt: this.$t('ALT.KEY_22'),
                    src: new URL('../assets/keys/22.png', import.meta.url).href
                },
                key22: {
                    alt: this.$t('ALT.KEY_23'),
                    src: new URL('../assets/keys/23.png', import.meta.url).href
                },
                key23: {
                    alt: this.$t('ALT.KEY_24'),
                    src: new URL('../assets/keys/24.png', import.meta.url).href
                }
            } as Record<string, TriviaDeath2.LoadableImage>,
            hasSubmitted: false,
            isSubmitting: false,
            selected: [] as number[]
        }
    },

    computed: {
        canSubmit() {
            if (this.isSubmitting) return false
            if (this.hasSubmitted) return false

            return true
        },

        choices() {
            if (!this.madness) return this.player.choices

            switch (this.madness) {
            case 'BTTF':
                return this.player.choices.map((choice) => ({
                    ...choice,
                    text: this.makeLettersDisappear(this.stripBBCode(choice.text))
                }))
            case 'NowThatsWhatICallMadness':
                return this.player.choices.map((choice) => ({
                    ...choice,
                    text: this.makeLettersDisappear(this.scramble(this.stripBBCode(choice.text)))
                }))
            case 'Scramble':
                return this.player.choices.map((choice) => ({
                    ...choice,
                    text: this.scramble(this.stripBBCode(choice.text))
                }))
            default:
                // this madness type doesn't modify each choice
                return this.player.choices
            }
        },

        prompt() {
            if (!this.madness) return this.player.prompt

            switch (this.madness) {
            case 'BTTF':
                return this.makeLettersDisappear(this.stripBBCode(this.player.prompt))
            case 'NowThatsWhatICallMadness':
                return this.makeLettersDisappear(this.scramble(this.stripBBCode(this.player.prompt)))
            case 'Scramble':
                return this.scramble(this.stripBBCode(this.player.prompt))
            default:
                // this madness type doesn't modify the prompt
                return this.player.prompt
            }
        },

        selections() {
            return this.player.choices
                .filter((_, index) => this.selected.includes(index))
                .map((choice) => choice.text)
                .join(', ')
        },

        showChoiceText() {
            if (this.player.category === 'postGame') return true
            if (this.player.category === 'wires') return true
            if (!this.player.category) return true

            return false
        }
    },

    watch: {
        'player.prompt': function resetSelection() {
            // As player receives new instructions during Math and Rules
            // minigames, reset visual state of previously-pressed button.
            this.selected = [] as number[]
        }
    },

    methods: {
        generateChoiceClasses(index: number, choice: TriviaDeath2.Choices.Choice) {
            const classes = []

            if (this.selected.indexOf(index) !== -1) {
                classes.push('selected')
            }

            if (choice.isFingerCutOff) {
                classes.push(`finger-cut-${index + 1}`)
            }

            if (choice.controllerClass) {
                classes.push(choice.controllerClass)
            }

            return classes
        },

        makeLettersDisappear(phrase: string) {
            let text = ''
            for (let i = 0; i < phrase.length; i++) {
                text += `[disappear]${phrase[i]}[/disappear]`
            }
            return text
        },

        scramble(phrase: string) {
            // remove spaces
            const letters = phrase.replace(/\s/gi, '').split('')

            // shuffle letters
            for (let i = letters.length; i > 0; i--) {
                const newIndex = Math.floor(Math.random() * (i + 1))
                const char = letters[i]
                letters[i] = letters[newIndex]
                letters[newIndex] = char
            }
            // add spaces back
            for (let i = 0; i < phrase.length; i++) {
                if (phrase.charAt(i) === ' ') {
                    letters.splice(i, 0, ' ')
                }
            }

            return letters.join('')
        },

        // Use with madness effects to avoid scrambling BBCode tokens
        // or wrapping them in <span>s meant to animate.
        stripBBCode(phrase: string) {
            // https://stackoverflow.com/a/1483715
            return phrase.replace(/\[\/?(?:b|i|u|url|quote|code|img|color|size)*?.*?\]/img, '')
        },

        async onSelect(index: number) {
            const selectionIndex = this.selected.indexOf(index)
            let action: 'select' | 'unselect' | 'submit'

            if (this.player.hasSubmit) {
                if (selectionIndex === -1) {
                    this.selected.push(index)
                    action = 'select'
                } else {
                    this.selected.splice(selectionIndex, 1)
                    action = 'unselect'
                }
            } else {
                this.selected.push(index)
                action = 'submit'
            }

            this.isSubmitting = true

            try {
                if (this.$ecast.role === 'player') {
                    await this.$ecast.updateObject<TriviaDeath2.Choices.Response>(
                        this.player.responseKey!,
                        {
                            action,
                            choice: index
                        }
                    )

                    // Prevent multiple quick selections for minigames with item choices like Gifts,
                    // without preventing multiple presses of buttons for minigames like Skull Dice.
                    if (this.player.category) {
                        this.hasSubmitted = true
                    }
                } else if (this.$ecast.role === 'audience' && action === 'submit') {
                    // Only incrementing the count group on a submit action
                    // ensures audience answers aren't counted multiple times
                    // during final round selections/deselections.

                    await this.$ecast.incrementCountGroupCounter(
                        this.player.countGroupKey!,
                        String(index)
                    )

                    this.hasSubmitted = true
                }
            } catch (error) {
                this.$handleEcastError(error)
            } finally {
                this.isSubmitting = false
            }
        },

        async onSubmit() {
            this.isSubmitting = true

            try {
                if (this.$ecast.role === 'player') {
                    await this.$ecast.updateObject<TriviaDeath2.Choices.Response>(
                        this.player.responseKey!,
                        {
                            action: 'submit'
                        }
                    )
                } else if (this.$ecast.role === 'audience') {
                    // TriviaDeath2AudienceChoice CountGroup counter keys
                    // include "" for no selection, "0", "1", "2" for single
                    // selections, and permutations like "0,2" for multiple
                    // selections. Those permutation keys use ascending numeric
                    // order. Audience-selected indexes must be sorted to match.
                    const selections = this.selected.sort((a, b) => a - b).join()

                    await this.$ecast.incrementCountGroupCounter(
                        this.player.countGroupKey!,
                        selections
                    )
                }

                this.hasSubmitted = true
            } catch (error) {
                this.$handleEcastError(error)
            } finally {
                this.isSubmitting = false
            }
        }
    }
})
</script>

