import simplify from 'simplify-js'
import type { DoodleEntity, WSClient } from '@jackboxgames/ecast'
import { Utils } from './Utils'

export class DoodleCanvas {
    private DEFAULT_WIDTH = 400
    private DEFAULT_HEIGHT = 400

    canvas: HTMLCanvasElement
    ctx: CanvasRenderingContext2D | null

    doodle: DoodleEntity

    brush = ''
    brushDrawFunctions = {} as Record<string, DoodleCanvas.DrawFn>
    color = '#000000'
    layer = 0
    points = [] as WSClient.DoodlePoint[]
    precision = 2
    scale = { width: 1, height: 1 } as DoodleCanvas.Scale
    tolerance = 0.5
    weight = 4
    weightScale = 1

    isInteracting = false

    // eslint-disable-next-line complexity
    constructor(canvas: HTMLCanvasElement, doodle: DoodleEntity, options?: DoodleCanvas.Options) {
        if (options?.brush) {
            this.brush = options.brush
        }
        if (options?.brushDrawFunctions) {
            this.brushDrawFunctions = options.brushDrawFunctions
        }
        if (options?.color) {
            this.color = options.color
        }
        if (options?.layer) {
            this.layer = options.layer
        }
        if (options?.precision) {
            this.precision = options.precision
        }
        if (options?.scale) {
            this.scale = options.scale
        }
        if (options?.tolerance) {
            this.tolerance = options.tolerance
        }
        if (options?.weight) {
            this.weight = options.weight
        }
        if (options?.weightScale) {
            this.weightScale = options.weightScale
        }

        // Scale so controller lines look nicer; game will receive unscaled points
        canvas.width = (doodle.size?.width ?? this.DEFAULT_WIDTH) * this.scale.width
        canvas.height = (doodle.size?.height ?? this.DEFAULT_HEIGHT) * this.scale.height
        this.canvas = canvas

        this.ctx = canvas.getContext('2d')
        this.ctx?.scale(this.scale.width, this.scale.height)

        this.doodle = doodle

        if (this.doodle.lines.length) {
            this.drawLines()
        }
    }

    addPoint(point: WSClient.DoodlePoint) {
        this.points.push(point)
    }

    normalizePoint(point: WSClient.DoodlePoint): WSClient.DoodlePoint {
        // Unscale point back to its expected position
        const unscaledPoint = {
            x: point.x / this.scale.width,
            y: point.y / this.scale.height
        }

        // Clamp point to stay within the drawing area
        const clampedPoint = {
            x: Math.min(
                Math.max(0.5 * this.weight, unscaledPoint.x),
                (this.canvas.width / this.scale.width) - 0.5 * this.weight
            ),
            y: Math.min(
                Math.max(0.5 * this.weight, unscaledPoint.y),
                (this.canvas.height / this.scale.height) - 0.5 * this.weight
            )
        }

        // Round point to desired decimal precision
        const roundedPoint = {
            x: Utils.toPrecision(clampedPoint.x, this.precision),
            y: Utils.toPrecision(clampedPoint.y, this.precision)
        }

        return roundedPoint
    }

    // drawLines renders lines in exact stroke order while only allowing a line
    // to overlap if it exists on the same or higher layer as a previous line.
    // It also handles transparent lines, which are real lines that mask
    // previous lines, to give the apperance of erasing.
    drawLines() {
        if (!this.ctx) return

        this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height)

        // Represent each layer as an in-memory canvas, ready to receive lines
        const maxLayer = this.doodle.maxLayer ?? 0
        const layerCanvases = Array.from({ length: maxLayer + 1 }, () => {
            const canvas = document.createElement('canvas')
            canvas.width = this.canvas.width
            canvas.height = this.canvas.height

            const ctx = canvas.getContext('2d')
            ctx?.scale(this.scale.width, this.scale.height)

            return { canvas, ctx }
        })

        // If a line is transparent (likely for "erasing"), it must be drawn on
        // its own layer AND all lower layers in order for the masking to apply
        // to all previously-drawn lines. This function draws this line onto the
        // appropriate in-memory canvases.
        //
        // Consumers of DoodleCanvas should only call `doodle/stroke` once with
        // this line's original layer, though.
        const drawTransparentLine = (line: WSClient.DoodleLine) => {
            if (line.color !== 'transparent') return

            const layer = line.layer != null ? line.layer : this.layer

            for (let i = 0; i <= layer; i++) {
                this.drawLine(line, layerCanvases[i].ctx)
            }
        }

        // Draw previous lines in order of appearance onto their layer canvas
        Object.values(this.doodle.lines).forEach((line) => {
            const layerCtx = layerCanvases[line.layer ?? 0].ctx

            if (line.color === 'transparent') {
                drawTransparentLine(line)
            } else {
                this.drawLine(line, layerCtx)
            }
        })

        // Draw the current line onto its layer canvas
        const currentLine = {
            brush: this.brush,
            color: this.color,
            index: this.doodle.lines.length,
            layer: this.layer,
            points: this.points,
            weight: this.weight
        }

        if (this.color === 'transparent') {
            drawTransparentLine(currentLine)
        } else {
            this.drawLine(currentLine, layerCanvases[currentLine.layer].ctx)
        }

        // Flatten all lines onto the on-screen canvas, from bottom layer to top
        layerCanvases.forEach(({ canvas }) => {
            this.ctx!.drawImage(canvas, 0, 0, this.canvas.width / this.scale.width, this.canvas.height / this.scale.height)
        })
    }

    drawLine(line: WSClient.DoodleLine, ctx: CanvasRenderingContext2D | null) {
        if (!ctx) return

        const customDrawFunction = line.brush && this.brushDrawFunctions[line.brush]
        if (customDrawFunction) {
            customDrawFunction(line, ctx)
            return
        }

        if (line.color === 'transparent') {
            ctx.globalCompositeOperation = 'destination-out'
        } else {
            ctx.fillStyle = line.color
            ctx.globalCompositeOperation = 'source-over'
            ctx.strokeStyle = line.color
        }

        ctx.lineCap = 'round'
        ctx.lineJoin = 'round'
        ctx.lineWidth = line.weight * this.weightScale

        ctx.beginPath()

        line.points.forEach((point, i) => {
            if (line.points.length === 1 && i === 0) {
                // HACK: Safari doesn't render 0 radius arcs or 0 distance lines.
                // Fill a radius so a dot is rendered.
                ctx.save()
                ctx.arc(point.x, point.y, line.weight / 2, 0, 2 * Math.PI)
                ctx.fill()
                ctx.restore()

                // Start new path to avoid stroking the arc
                ctx.beginPath()
            }

            ctx.lineTo(point.x, point.y)
        })

        ctx.stroke()
    }

    renderImage(imageFormat: DoodleCanvas.ImageFormat = 'image/png') {
        // Lines are drawn at instantiation and during events,
        // but just to make sure...
        if (this.doodle.lines.length > 0) {
            this.drawLines()
        }

        return this.canvas.toDataURL(imageFormat)
    }

    onStart(initialPoint: WSClient.DoodlePoint) {
        this.isInteracting = true

        const normalizedPoint = this.normalizePoint(initialPoint)
        this.addPoint(normalizedPoint)
        this.drawLines()
    }

    onMove(point: WSClient.DoodlePoint) {
        if (!this.isInteracting) return

        const lastPoint = this.points[this.points.length - 1]
        if (!lastPoint) {
            // Unusual; a point should've been added on start.
            // Treat the current point as the initial one, then bail.
            this.addPoint(this.normalizePoint(point))
            return
        }

        // Calculate distance to the brush when the pointer position updates. If
        // this distance is larger than the defined radius, move the brush
        // `distance - radius` pixels in the direction where the pointer is.
        const radius = 0.5 * this.weight
        const vector = {
            x: point.x - lastPoint.x,
            y: point.y - lastPoint.y
        }

        const distance = Math.sqrt(vector.x ** 2 + vector.y ** 2)

        if (distance > radius) {
            const ratio = (distance - radius) / distance
            const edgeVector = {
                x: vector.x * ratio,
                y: vector.y * ratio
            }
            const edgePoint = {
                x: lastPoint.x + edgeVector.x,
                y: lastPoint.y + edgeVector.y
            }

            this.addPoint(this.normalizePoint(edgePoint))
            this.drawLines()
        }
    }

    onEnd(): WSClient.DoodleLine | null {
        if (!this.isInteracting) return null

        const line = {
            brush: this.brush,
            color: this.color,
            index: this.doodle.lines.length,
            layer: this.layer,
            points: simplify(this.points, this.tolerance).map((point) => ({
                x: Utils.toPrecision(point.x, this.precision),
                y: Utils.toPrecision(point.y, this.precision)
            })),
            weight: this.weight
        } as WSClient.DoodleLine

        this.isInteracting = false
        this.points = []

        return line
    }
}

export namespace DoodleCanvas {
    export type DrawFn = (line: WSClient.DoodleLine, ctx: CanvasRenderingContext2D) => void

    export type ImageFormat = 'image/jpg' | 'image/png' | 'image/webp'

    export interface Options {
        brush?: string
        brushDrawFunctions?: Record<string, DrawFn>
        color?: string
        layer?: number
        precision?: number
        scale?: Scale
        tolerance?: number
        weight?: number
        weightScale?: number
    }

    export interface Scale {
        width: number,
        height: number
    }
}
