class GCounter {
    constructor(options) {
        this.key = options.key;
        this.count = options.count;
        this.meta = options.meta || {};
    }

    whenReceived(client) {
        client.entities[this.key] = this;
    }

    toString() { return `GCounter{\n\tcount:${this.count}\n\tmeta:${this.meta}\n}`; }
}

module.exports = { GCounter };
