<template>
<!-- eslint-disable-next-line vuejs-accessibility/form-control-has-label -->
<textarea
    ref="textarea"
    :rows="rows"
    :value="modelValue"
    v-on:input="onInput"
    v-on:keypress.enter="onKeypressEnter"
/>
</template>

<script lang="ts">

/**
 * A textarea wrapper component that:
 *   - Abstracts the weird way Android soft keyboards handle keyboard events
 *   - Strips newline characters
 *   - Can automatically resize if desired (via `autosize` prop)
 *
 * This component can be used exactly as if it was a textarea itself.
 * Make sure you associate a <label> with it!
 */

import { defineComponent } from 'vue'
import autosize from 'autosize'
import 'core-js/features/string/replace-all'
import { Sanitize } from '@tv/shared'

export default defineComponent({
    props: {
        autosize: Boolean,
        modelValue: String,
        rows: {
            type: Number,
            default: 1
        },
        sanitizers: Array as Prop<Sanitize.Type[]>
    },

    emits: {
        'keypress': (_: KeyboardEvent) => true,
        'update:modelValue': (_: string) => true
    },

    watch: {
        modelValue(newValue, oldValue) {
            if (newValue !== oldValue) {
                const input = this.$refs.textarea as HTMLTextAreaElement
                input.value = newValue
            }
        }
    },

    mounted() {
        if (this.autosize) {
            autosize(this.$refs.textarea as HTMLTextAreaElement)
        }
    },

    methods: {
        // HACK: Vue 3 changed how this component's $el works. For some reason,
        // it is a `#text` node, NOT the <textarea> input. Adding this method
        // allows parent components to focus the actual HTMLTextAreaElement.
        focus() {
            const textarea = this.$refs.textarea as HTMLTextAreaElement
            textarea.focus()
        },

        async onInput(event: Event) {
            const target = event.target as HTMLTextAreaElement | null

            // some janky browsers fire input events without a defined target or
            // a valid string value, so don't try to do anything with those
            if (target?.value == null) return

            const maxLength = target.maxLength === -1 ? Number.MAX_SAFE_INTEGER : target.maxLength

            // strip all newline characters beyond the maximum rows supported
            const lines = target.value.split('\n')
            if (lines.length > this.rows) {
                let result = ''
                for (let i = 0; i < this.rows - 1; i++) {
                    result += `${lines[i]}\n`
                }
                result += lines.slice(this.rows - 1).join(' ').replaceAll('\n', '')

                target.value = result
            }

            if (this.sanitizers) {
                target.value = Sanitize.withTypes(target.value, [...this.sanitizers])
            }

            if (target.value.length > maxLength) {
                target.value = target.value.substring(0, maxLength)
                return
            }

            this.$emit('update:modelValue', target.value)

            // wait one tick to allow any listeners to modify the value and then
            // apply the change
            await this.$nextTick()
            if (target.value !== this.modelValue) {
                target.value = this.modelValue!
            }
        },

        onKeypressEnter(event: KeyboardEvent) {
            // allow parent components to call custom behavior on Enter,
            // e.g. shifting focus to the next input during Thriplash
            this.$emit('keypress', event)
        }
    }

})
</script>
