<template>
<div class="grid-selecting">
    <span v-bb="player.prompt" class="message prompt" />
    <div v-for="(row, yIndex) in player.grid" :key="`row-${yIndex}`" class="x-row">
        <div v-for="(button, xIndex) in row" :key="`item-${xIndex}-${yIndex}`" class="y-column">
            <div
                v-if="button.type === 'disabled'"
                class="spacer"
            />
            <button
                v-else-if="button.type !== 'hidden'"
                class="action grid-button"
                :class="getButtonClass(xIndex, yIndex, button.type)"
                :disabled="isSubmitting"
                v-on:click="onSubmit(xIndex, yIndex)"
            >
                <Arrow
                    v-if="button.type ==='arrow'"
                    class="arrow-image"
                />
            </button>
        </div>
    </div>
    <span v-bb="player.error" class="message error" />
    <img
        v-if="player.swordLength && images[`swordLength${player.swordLength}`]"
        class="sword-image"
        :src="images[`swordLength${player.swordLength}`].src"
        :alt="images[`swordLength${player.swordLength}`].alt"
    >
</div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'

import type { TriviaDeath2 } from '../TriviaDeath2'

import Arrow from '../assets/skewers/arrow.svg.vue'

export default defineComponent({

    components: {
        Arrow
    },

    props: {
        player: {
            type: Object as Prop<TriviaDeath2.GridSelecting.Entity>,
            required: true
        }
    },

    data() {
        return {
            images: {
                swordLength2: {
                    alt: this.$t('ALT.SWORD_2'),
                    src: new URL('../assets/skewers/length2.png', import.meta.url).href
                },
                swordLength3: {
                    alt: this.$t('ALT.SWORD_3'),
                    src: new URL('../assets/skewers/length3.png', import.meta.url).href
                },
                swordLength4: {
                    alt: this.$t('ALT.SWORD_4'),
                    src: new URL('../assets/skewers/length4.png', import.meta.url).href
                }
            } as Record<string, TriviaDeath2.LoadableImage>,
            isSubmitting: false
        }
    },

    methods: {
        getButtonClass(x: number, y: number, type: string) {
            if (type === 'arrow') {
                if (y !== 0 && x === 0) {
                    return [type, 'right']
                }

                if (y !== 0 && x !== 0) {
                    return [type, 'left']
                }
            }

            return [type]
        },

        async onSubmit(x: number, y: number) {
            this.isSubmitting = true

            try {
                await this.$ecast.updateObject<TriviaDeath2.GridSelecting.Response>(
                    this.player.responseKey,
                    {
                        action: 'select',
                        x,
                        y
                    }
                )
            } catch (error) {
                this.$handleEcastError(error)
            } finally {
                this.isSubmitting = false
            }
        }
    }
})
</script>

<style lang="scss" scoped>
