    display: flex;
    flex-direction: column;

    .error {
        width: 100%;
        margin-top: 10px;
        background-color: white;
        color: red !important;
        font-size: 24px;
    }

    .prompt {
        position: relative;
        top: -10px;
        left: calc(-50vw + 50%);
        width: 100vw;
        padding: 10px 15px;
        background-color: white;
        font-size: 2.4rem;
        line-height: 2.4rem;
    }

    .x-row {
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 4px;
        width: 100%;
        margin: 2px auto;
    }

    .y-column {
        width: clamp(50px, 15vw, 90px);
        height: clamp(50px, 15vw, 90px);
        min-width: 50px;
        min-height: 50px;
    }

    .action,
    .spacer {
        width: clamp(50px, 15vw, 90px);
        height: clamp(50px, 15vw, 90px);
        border: none;
        border-radius: 5px;
        background-color: black;
    }

    .action:disabled,
    .spacer {
        opacity: 60%;
    }

    .grid-button {
        &.arrow {
            display: flex;
            align-items: flex-end;
            width: clamp(38px, 11vw, 68px);
            height: clamp(50px, 15vw, 90px);
            margin: 0 auto;

            &.left {
                transform: rotate(90deg);
            }

            &.right {
                transform: rotate(-90deg);
            }

            .arrow-image {
                margin: 0 auto;
                padding-bottom: 5px;
                width: 75%;
            }
        }
    }

    .sword-image {
        height: clamp(50px, 15vw, 90px);
        margin: 15px 0;
        object-fit: scale-down;
    }
}
</style>
