<template>
<div class="single-text-entry">
    <div class="constrain">
        <p v-if="player!.prompt" v-bb="player!.prompt" />
        <label v-if="player!.label" v-bb="player!.label" for="input" />

        <TextArea
            v-if="player!.isMultiline"
            id="input"
            :disabled="player!.isDisabled"
            :model-value="value"
            :placeholder="player!.placeholder"
            :rows="player!.lines || 2"
            v-on:update:model-value="onWrite"
        />
        <TextInput
            v-else
            id="input"
            type="text"
            :disabled="player!.isDisabled"
            :placeholder="player!.placeholder"
            :model-value="value"
            v-on:update:model-value="onWrite"
        />

        <button v-bb="player!.submitText || 'SUBMIT'" v-on:click.prevent="onSubmit" />
    </div>
</div>
</template>


<script lang="ts">
import { defineComponent } from 'vue'
import { ObjectEntity } from '@jackboxgames/ecast'

import { CommonEntities } from '@tv/shared'

import TextInput from './TextInput.vue'
import TextArea from './TextArea.vue'

export default defineComponent({

    components: {
        TextArea,
        TextInput
    },

    props: {
        player: Object as Prop<CommonEntities.SingleTextEntry>
    },

    data() {
        return {
            value: ''
        }
    },

    methods: {
        isObjectResponseKey() {
            // Try to auto-detect the responseType
            if (!this.player!.responseType) {
                const responseEntity = this.$ecast.entities[this.player!.responseKey]

                if (responseEntity && responseEntity instanceof ObjectEntity) {
                    return true
                }
            }

            return this.player!.responseType === 'object'
        },

        send() {
            if (this.isObjectResponseKey()) {
                void this.sendAsObject()
                return
            }

            void this.sendAsText()
        },

        async sendAsObject() {
            const data = this.player!.send || { action: this.player!.action || 'input' }
            data[this.player!.key || 'value'] = this.value

            try {
                await this.$ecast.updateObject(this.player!.responseKey, data)
            } catch (error) {
                this.$handleEcastError(error)
            }
        },

        async sendAsText() {
            try {
                await this.$ecast.updateText(this.player!.responseKey, this.value)
            } catch (error) {
                this.$handleEcastError(error)
            }
        },

        async onSubmit() {
            this.send()

            if (!this.player?.preventResponseLock) {
                try {
                    await this.$ecast.lock(this.player!.responseKey)
                } catch (error) {
                    this.$handleEcastError(error)
                }
            }
        },

        onWrite(text: string) {
            this.value = text

            if (this.player!.live) {
                // Send after each update, for autosubmit
                void this.send()
            }
        }
    }
})
</script>
