class TextRing {
    constructor(options) {
        this.key = options.key;
        this.elements = options.elements;
        this.limit = options.limit;
        this.meta = options.meta || {};
    }

    whenReceived(client) {
        client.entities[this.key] = this;
    }

    toString() { return `TextRing{\n\telements: ${this.elements}\n\tmeta:${JSON.stringify(this.meta)}\n}`; }
}

module.exports = { TextRing };