<template>
<div class="writing">
    <span v-bb="player.prompt" class="message prompt" />
    <form v-on:submit.prevent="onSubmit">
        <div v-if="player.type === 'password'" class="hint-wrapper">
            <div
                v-for="(hint, index) in player.hints"
                :key="`hintgroup-${index}`"
                class="password-hint"
            >
                <span
                    v-for="(hintElement, elementIndex) in hint"
                    :key="`hint-${elementIndex}`"
                    v-bb="hintElement"
                    class="hint-element"
                    :class="hintElement && hintElement !== '' ? 'active' : 'inactive'"
                />
            </div>
        </div>
        <TextInput
            v-if="player.type === 'donation'"
            v-model="currentText"
            aria-label="input amount to donate"
            class="player-input"
            min="0"
            max="500"
            :placeholder="player.placeholder"
            type="number"
            v-on:update:model-value="onInput"
        />
        <TextArea
            v-else
            ref="textarea"
            v-model="currentText"
            aria-label="text input"
            class="player-input"
            :maxlength="player.maxLength"
            :placeholder="player.placeholder"
            :rows="player.type === 'dictation' ? 3 : 1"
            v-on:update:model-value="onInput"
            v-on:keydown.enter.prevent="onSubmit"
        />
        <span v-bb="error" class="message error" />
        <button
            v-if="player.type !== 'dictation'"
            v-t="'ACTION.SUBMIT'"
            class="submit"
        />
    </form>
</div>
</template>

<script lang="ts">
import { EcastFilterError } from '@jackboxgames/ecast/errors'
import { defineComponent } from 'vue'

import { TextArea, TextInput } from '@tv/vue'

import type { TriviaDeath2 } from '../TriviaDeath2'

export default defineComponent({

    components: {
        TextArea,
        TextInput
    },

    props: {
        player: {
            type: Object as Prop<TriviaDeath2.Writing.Entity>,
            required: true
        }
    },

    data() {
        return {
            currentText: '',
            error: '',
            isSubmitting: false
        }
    },

    watch: {
        'player.error': function showError(error: string) {
            this.error = error
        }
    },

    methods: {
        resetError() {
            if (this.error) {
                this.error = ''
            }
        },

        async onInput(text: string) {
            this.resetError()

            try {
                await this.$ecast.updateText(this.player.textKey, text)
            } catch (error) {
                // Ignore filter errors until a player goes to submit.
                if (!(error instanceof EcastFilterError)) {
                    this.$handleEcastError(error)
                }
            }
        },

        async onSubmit() {
            if (this.player.type === 'dictation') return

            this.isSubmitting = true

            try {
                await this.$ecast.updateText(this.player.textKey, this.currentText)
                await this.$ecast.updateObject<TriviaDeath2.Choices.Response>(
                    this.player.responseKey,
                    { action: 'submit' }
                )
            } catch (error) {
                if (error instanceof EcastFilterError) {
                    this.error = this.$t('ERROR.TEXT_NAUGHTY')
                    return
                }

                this.$handleEcastError(error)
            } finally {
                this.isSubmitting = false

                if (this.player.type === 'password') {
                    this.currentText = ''

                    // Set focus on textarea for rapid guessing
                    await this.$nextTick()
                    const textarea = this.$refs.textarea as InstanceType<typeof TextArea>
                    textarea.focus()
                }
            }
        }
    }


})
</script>

