<template>
<div class="multi-text-entry">
    <div class="constrain">
        <p v-if="player!.prompt" v-bb="player!.prompt" />

        <template v-for="(input, index) in player!.inputs">
            <label v-if="input.label" :key="`label-${input.key}`" v-bb="input.label" :for="`input-${index}`" />

            <textarea
                v-if="input.isMultiline"
                :id="`input-${index}`"
                :key="`input-${index}`"
                :value="values[index]"
                :rows="input.lines || 2"
                :placeholder="input.placeholder"
                :disabled="input.isDisabled"
                v-on:input="onValueInput($event, index)"
            />
            <input
                v-else
                :id="`input-${index}`"
                :key="`input-${input.key}`"
                type="text"
                :value="values[index]"
                :placeholder="input.placeholder"
                :disabled="input.isDisabled"
                v-on:input="onValueInput($event, index)"
            >
        </template>

        <button v-bb="player!.submitText || 'SUBMIT'" v-on:click.prevent="onSubmitClick" />
    </div>
</div>
</template>


<script lang="ts">
import { defineComponent } from 'vue'
import { ObjectEntity } from '@jackboxgames/ecast'
import { CommonEntities } from '@tv/shared'

export default defineComponent({
    props: {
        player: Object as Prop<CommonEntities.MultiTextEntry>
    },

    data() {
        return {
            values: <string[]> []
        }
    },

    beforeMount() {
        this.values = this.player!.inputs.map((input) => input.value ?? '')
    },

    methods: {
        onValueInput(event: Event, index: number) {
            const target = event.target as HTMLInputElement
            this.values[index] = target.value
        },

        isObjectResponseKey(): boolean {
            // Try to auto-detect the responseType
            if (!this.player!.responseType) {
                const responseEntity = this.$ecast.entities[this.player!.responseKey]

                if (responseEntity && responseEntity instanceof ObjectEntity) {
                    return true
                }
            }

            return this.player!.responseType === 'object'
        },

        onSubmitClick() {
            if (this.isObjectResponseKey()) {
                this.sendAsObject()
                return
            }

            this.sendAsText()
        },

        sendAsObject() {
            const data = this.player!.send || { action: this.player!.action || 'input' }
            this.player!.inputs.forEach((input, index) => {
                const key = input.key ?? `input${index}`
                data[key] = this.values[index]
            })
            void this.$ecast.updateObject(this.player!.responseKey, data).catch(this.$handleEcastError)
        },

        sendAsText() {
            const seperator = this.player!.separator || '|'
            void this.$ecast.updateText(this.player!.responseKey, this.values.join(seperator)).catch(this.$handleEcastError)
        }
    }
})
</script>
