<template>
<div class="dropping">
    <span v-t="'DROPPING.PROMPT'" class="message prompt" />
    <canvas id="drop" width="640" height="640" />
</div>
</template>

<script lang="ts">
import {
    GameLoop,
    Sprite,
    getCanvas,
    getContext,
    getPointer,
    init,
    initPointer,
    pointerPressed
} from 'kontra'
import { defineComponent } from 'vue'

import type { TriviaDeath2 } from '../TriviaDeath2'

const PUCK_SIZE = 128

export default defineComponent({

    props: {
        color: {
            type: String,
            required: true
        },
        player: {
            type: Object as Prop<TriviaDeath2.Dropping.Entity>,
            required: true
        }
    },

    data() {
        const puckImage = new Image(PUCK_SIZE, PUCK_SIZE)
        puckImage.src = new URL(`../assets/chips/${this.color}.png`, import.meta.url).href

        return {
            isSubmitting: false,
            percent: 0,

            // Kontra classes
            bar: {
                anchor: { x: 0.5, y: 0.5 },
                height: 20,
                color: 'black'
            },
            puck: {
                anchor: { x: 0.5, y: 0.5 },
                x: 320,
                y: 0.5 * PUCK_SIZE,

                ddy: 0,

                image: puckImage,

                isDropped: false,
                isSelected: false,

                dropped() {
                    // intentional no-op, will be replaced after canvas and
                    // Sprite initialization
                },

                onUp() {
                    if (!this.isSelected) return
                    this.isSelected = false
                    this.ddy = 9 * 60
                    this.isDropped = true
                    this.dropped()
                },

                render() {
                    // FIXME: Sprite#draw will exist when these properties are
                    // used to construct an instance, but referencing it here
                    // upsets TS.
                    this.draw()

                    // Draw a triangle at the bottom of the chip
                    if (!this.isDropped) {
                        const context = getContext()
                        context.save()
                        context.translate(0.5 * PUCK_SIZE, PUCK_SIZE)
                        context.fillStyle = 'white'
                        context.beginPath()
                        context.moveTo((-1 / 6) * PUCK_SIZE, 0)
                        context.lineTo((1 / 6) * PUCK_SIZE, 0)
                        context.lineTo(0, (1 / 6) * PUCK_SIZE)
                        context.fill()
                        context.restore()
                    }
                },

                update(dt?: number) {
                    if (this.isSelected && !pointerPressed('left')) {
                        this.onUp()
                    }
                    if (!this.isDropped && pointerPressed('left')) {
                        const pointer = getPointer()
                        this.x = Math.max(
                            0.5 * PUCK_SIZE,
                            Math.min(
                                getCanvas().width - 0.5 * PUCK_SIZE,
                                pointer.x
                            )
                        )
                        this.isSelected = true
                    }

                    // FIXME: Sprite#advance will exist when these properties
                    // are used to construct an instance, but referencing it
                    // here upsets TS.
                    this.advance(dt)
                }
            },

            // Kontra data
            gameLoop: null as GameLoop | null,
            sprites: [] as Sprite[]
        }
    },

    mounted() {
        init('drop')
        initPointer()

        this.gameLoop = GameLoop({
            fps: 60,

            update: (dt) => {
                this.sprites.forEach((s) => s.update(dt))
                this.sprites = this.sprites.filter((s) => s.isAlive())
            },

            render: () => {
                this.sprites.forEach((s) => s.render())
            }
        })

        for (let i = 0; i < this.sprites.length; i++) {
            this.sprites[i].ttl = 0 // reset
        }

        const b = Sprite(this.bar)
        b.x = getCanvas().width * 0.5
        b.y = 0.5 * PUCK_SIZE
        b.width = getCanvas().width - PUCK_SIZE
        this.sprites.push(b)

        const p = Sprite(this.puck)
        getCanvas().addEventListener('mouseleave', this.puck.onUp)

        p.dropped = () => {
            const min = 0.5 * PUCK_SIZE
            const max = getCanvas().width - 0.5 * PUCK_SIZE
            const total = max - min
            this.percent = Math.max(
                0,
                Math.min(
                    100,
                    Math.floor(((p.x - min) / total) * 100)
                )
            )

            void this.onDrop()
        }
        this.sprites.push(p)

        this.gameLoop.start()
    },

    beforeUnmount() {
        for (let i = 0; i < this.sprites.length; i++) {
            this.sprites[i].ttl = 0 // reset
        }
        this.gameLoop?.update(1 / 60)
        this.gameLoop?.stop()
    },

    methods: {
        async onDrop() {
            this.isSubmitting = true

            try {
                await this.$ecast.updateObject<TriviaDeath2.Dropping.Response>(
                    this.player.responseKey,
                    {
                        action: 'drop',
                        value: this.percent
                    }
                )
            } catch (error) {
                this.$handleEcastError(error)
            }
        }
    }
})
</script>

<style lang="scss" scoped>
.dropping {
    display: flex;
    flex-direction: column;

    canvas {
        width: 100%;
        align-self: center;
    }

    .prompt {
        position: relative;
        top: -10px;
        left: calc(-50vw + 50%);
        width: 100vw;
        padding: 10px 15px;
        background-color: white;
        font-size: 2.4rem;
        line-height: 2.4rem;
    }
}
</style>
