class CountGroup {
    constructor(options) {
        this.choices = options.choices;
        this.key = options.key;
        this.meta = options.meta || {};
    }

    whenReceived(client) {
        client.entities[this.key] = this;
    }

    toString() { return `CountGroup{\n\tchoices: ${this.choices}\n\tmeta:${JSON.stringify(this.meta)}\n}`; }
}

module.exports = { CountGroup };
