<template>
<div class="tmp2" :class="classes">
    <div class="play-area">
        <span class="name-header" v-text="name" />
        <div class="stage">
            <div class="constrain">
                <component :is="screen.name" v-bind="screen.props" />
            </div>
        </div>
    </div>
</div>
</template>


<script lang="ts">
import { defineComponent } from 'vue'

import type { CommonEntities } from '@tv/shared'

import Choices from './Choices.vue'
import Dialing from './Dialing.vue'
import Drawing from './Drawing.vue'
import Dropping from './Dropping.vue'
import GridSelecting from './GridSelecting.vue'
import Lobby from './Lobby.vue'
import Scratching from './Scratching.vue'
import Waiting from './Waiting.vue'
import Writing from './Writing.vue'

import type { TriviaDeath2 } from '../TriviaDeath2'

export default defineComponent({

    // WARN: TMP2 game code doesn't implement a post-game handler. Instead,
    // it uses a bespoke combination of Choices and Waiting to show game-end
    // information.
    //
    // GOOGLE ANALYTICS FOR POST-GAME VIEWS ARE UNTRUSTWORTHY FOR TMP2
    components: {
        Lobby,

        Choices,
        Dialing,
        Drawing,
        Dropping,
        GridSelecting,
        Scratching,
        Waiting,
        Writing
    },

    ecastKeys: {
        audience: 'audiencePlayer',
        info: ({ id }) => `info:${id}`,
        player: ({ id }) => `player:${id}`
    },

    ecastProviders: {
        artifact: (values) => {
            if (values.artifact) return values.artifact

            let counter = 0
            let value: CommonEntities.Artifact | undefined

            while (values[`artifact:${counter}`]) {
                value = values[`artifact:${counter}`]
                counter += 1
            }

            return value
        }
    },

    props: {
        artifact: Object as Prop<CommonEntities.Artifact>,
        audience: Object as Prop<CommonEntities.PlayerBase>,
        info: Object as Prop<TriviaDeath2.Info>,
        player: Object as Prop<CommonEntities.PlayerBase>
    },

    computed: {
        classes() {
            const classes = []

            if (this.info) {
                classes.push(this.info.color, this.info.madness)
            }

            if (this.$ecast.role === 'audience') {
                classes.push('audience')
            }

            if (this.player?.kind === 'waiting' || this.audience?.kind === 'waiting') {
                classes.push('waiting')
            }

            return classes
        },

        name() {
            if (this.info) {
                return this.info.name
            }

            if (this.$ecast.role === 'audience') {
                return this.$t('AUDIENCE.NAME')
            }

            return ''
        },

        /* eslint-disable complexity */
        screen() {
            if (this.$ecast.role === 'audience' && this.audience) {
                switch (this.audience.kind) {
                case 'choices':
                    return {
                        name: 'Choices',
                        props: {
                            player: this.audience
                        }
                    }
                case 'waiting':
                    return {
                        name: 'Waiting',
                        props: {
                            artifact: this.artifact,
                            player: this.audience
                        }
                    }
                }
            } else if (this.$ecast.role === 'player' && this.player) {
                switch (this.player.kind) {
                case 'lobby':
                    return {
                        name: 'Lobby',
                        props: {
                            artifact: this.artifact,
                            madness: this.info?.madness,
                            player: this.player
                        }
                    }
                case 'choices':
                    return {
                        name: 'Choices',
                        props: {
                            player: this.player
                        }
                    }
                case 'dialing':
                    return {
                        name: 'Dialing',
                        props: {
                            player: this.player
                        }
                    }
                case 'drawing':
                    return {
                        name: 'Drawing',
                        props: {
                            player: this.player
                        }
                    }
                case 'dropping':
                    return {
                        name: 'Dropping',
                        props: {
                            color: this.info?.color,
                            player: this.player
                        }
                    }
                case 'gridSelecting':
                    return {
                        name: 'GridSelecting',
                        props: {
                            player: this.player
                        }
                    }
                case 'scratching':
                    return {
                        name: 'Scratching',
                        props: {
                            player: this.player
                        }
                    }
                case 'waiting':
                    return {
                        name: 'Waiting',
                        props: {
                            artifact: this.artifact,
                            player: this.player
                        }
                    }
                case 'writing':
                    return {
                        name: 'Writing',
                        props: {
                            player: this.player
                        }
                    }
                }
            }

            return {
                name: 'Waiting',
                props: {}
            }
        }
        /* eslint-enable complexity */
    }
})
</script>


