    background-image: url("../assets/gallery.png");
    border-radius: 15px;

    .locale-de & {
        background-image: url("../assets/gallery-de.png");
    }
    .locale-es &,
    .locale-es-XL & {
        background-image: url("../assets/gallery-es.png");
    }
    .locale-fr & {
        background-image: url("../assets/gallery-fr.png");
    }
    .locale-it & {
        background-image: url("../assets/gallery-it.png");
    }
