<template>
<div class="dialing">
    <canvas id="phone" class="canvas phone" width="825" height="972" />
    <button id="reset" v-t="'ACTION.RESET'" class="action reset" v-on:click="onReset" />
</div>
</template>

<script lang="ts">
import {
    GameLoop,
    Sprite,
    getCanvas,
    getContext,
    getPointer,
    init,
    initPointer,
    pointerPressed,
    track,
    untrack
} from 'kontra'
import { defineComponent } from 'vue'

import type { TriviaDeath2 } from '../TriviaDeath2'

const FPS = 60
const BOUNCE_ANIM = [
    0, 0.178, 0.424, 0.608, 0.735, 0.824, 0.888, 0.934, 0.965, 0.986,
    0.997, 1, 0.997, 0.988, 0.973, 0.951, 0.921, 0.883, 0.836, 0.779,
    0.712, 0.632, 0.54, 0.436, 0.322, 0.201, 0.087, 0, 0.015, 0.032,
    0.049, 0.066, 0.083, 0.098, 0.112, 0.125, 0.136, 0.145, 0.151,
    0.154, 0.151, 0.144, 0.134, 0.121, 0.106, 0.09, 0.072, 0.054,
    0.035, 0.017, 0
]
const MAX_BOUNCE_ROT = 13

const degreesToRadians = function degreesToRadians(deg: number) {
    return deg * (Math.PI / 180)
}

export default defineComponent({

    props: {
        player: {
            type: Object as Prop<TriviaDeath2.Dialing.Entity>,
            required: true
        }
    },

    data() {
        const fingerStopImage = new Image()
        fingerStopImage.src = new URL('../assets/dial/fingerStop.png', import.meta.url).href

        const fingerWheelImage = new Image()
        fingerWheelImage.src = new URL('../assets/dial/fingerWheel.png', import.meta.url).href

        const numberPlateImage = new Image()
        numberPlateImage.src = new URL('../assets/dial/numberPlate.png', import.meta.url).href

        return {
            status: null,

            // Kontra classes
            dialedLabel: {
                id: 'dialedLabel',

                x: 0,
                y: 35,

                dialed: '',
                status: '',

                flash: true,
                flashFrames: 0,

                update() {
                    if (this.status) {
                        this.flashFrames += -1
                        if (this.flashFrames < 0) {
                            this.flash = !this.flash
                            this.flashFrames = 20
                        }
                    } else {
                        this.flash = false
                    }
                },

                render() {
                    if (this.flash) return
                    const display = this.status || this.dialed
                    const ctx = getContext()
                    ctx.save()
                    ctx.font = '48px nokian11regular'

                    // Shadow
                    ctx.fillStyle = '#6b8d45'
                    ctx.textAlign = 'center'
                    ctx.fillText(display, getCanvas().width * 0.5 + 1, this.y + 2)

                    // Text
                    ctx.fillStyle = '#1c3c29'
                    ctx.textAlign = 'center'
                    ctx.fillText(display, getCanvas().width * 0.5, this.y)

                    ctx.restore()
                }
            },

            fingerStop: {
                anchor: { x: 0.5, y: 0.5 },
                height: 700,
                width: 700,
                x: 412,
                y: 485,

                image: fingerStopImage
            },

            fingerWheel: {
                anchor: { x: 0.5, y: 0.5 },
                height: 700,
                width: 700,
                x: 412,
                y: 485,

                currentDigit: 0,
                isSelected: false,

                image: fingerWheelImage,

                maxRotation: 0,
                pointAngle: 0,
                rotation: 0,
                rotationSpeed: degreesToRadians(300) / FPS,
                startAngle: 0,

                bounceFrame: 0,

                onDown() {
                    // Ignore clicks if it's rotated or bouncing
                    if (this.rotation > 0) return
                    if (this.bounceFrame > 0) return

                    const pointer = getPointer()

                    const point = {
                        x: pointer.x - this.x,
                        y: pointer.y - this.y
                    }
                    let angle = Math.atan2(point.y, point.x)
                    // angle's gotta be ccw from the +x axis
                    if (angle < 0) {
                        angle = Math.abs(angle)
                    } else {
                        angle = 2 * Math.PI - angle
                    }

                    const currentDigit = Math.floor(angle / degreesToRadians(30))
                    // Ignore < 1 clicks
                    if (currentDigit === 0 || currentDigit === 11) return

                    // Move the max rotation up to the nearest 30deg
                    this.startAngle = angle
                    this.maxRotation = (currentDigit + 1) * degreesToRadians(30)
                    this.currentDigit = currentDigit
                    this.isSelected = true
                },

                onUp() {
                    if (!this.isSelected) return
                    this.isSelected = false
                    let currentDigit = Math.floor(this.rotation / degreesToRadians(30))
                    if (currentDigit === 0) this.bounceFrame = BOUNCE_ANIM.length - 1
                    if (currentDigit === 0 || currentDigit === 11) return
                    if (currentDigit === 10) currentDigit = 0
                    this.dialNumber(currentDigit)
                },

                dialNumber(num: number) {
                    // intentional no-op, will be replaced after canvas and
                    // Sprite initialization
                },

                update() {
                    // Finger moved
                    if (this.isSelected) {
                        const pointer = getPointer()

                        const point = {
                            x: pointer.x - this.x,
                            y: pointer.y - this.y
                        }
                        let angle = Math.atan2(point.y, point.x)
                        if (angle < 0) {
                            angle = Math.abs(angle)
                        } else {
                            angle = 2 * Math.PI - angle
                        }
                        this.pointAngle = angle

                        let newRotation = this.startAngle - angle
                        if (newRotation < 0) {
                            newRotation += 2 * Math.PI
                        }
                        if (newRotation > 0 && newRotation < this.maxRotation) {
                            this.rotation = newRotation
                        }

                        // Finger released
                        if (!pointerPressed('left')) {
                            this.onUp()
                        }
                    }

                    if (!this.isSelected) {
                        this.rotation -= this.rotationSpeed
                        this.rotation = Math.max(0, this.rotation)
                    }

                    if (this.bounceFrame > 0) {
                        const bounceFrame = (BOUNCE_ANIM.length - 1) - this.bounceFrame
                        this.rotation = degreesToRadians(MAX_BOUNCE_ROT * BOUNCE_ANIM[bounceFrame])
                        this.bounceFrame += -1
                    }
                }
            },

            numberPlate: {
                anchor: { x: 0.5, y: 0.5 },
                height: 972,
                width: 825,
                x: 412,
                y: 486,

                image: numberPlateImage
            },

            // Kontra data
            gameLoop: null as GameLoop | null,
            sprites: [] as Sprite[]
        }
    },

    watch: {
        'player.dialed': function updateDialedLabel(dialed: string) {
            this.setDialedLabel(dialed)
        }
    },

    mounted() {
        init('phone')
        initPointer()

        this.gameLoop = GameLoop({
            fps: FPS,
            update: (dt) => {
                this.sprites.forEach((s) => s.update(dt))
                this.sprites = this.sprites.filter((s) => s.isAlive())
            },
            render: () => {
                this.sprites.forEach((s) => s.render())
            }
        })

        const p = Sprite(this.numberPlate)
        this.sprites.push(p)

        const w = Sprite(this.fingerWheel)
        w.dialNumber = (num: number) => {
            // When possible, vibrate phone as though player is placing a call
            const pattern = []
            const patternCount = Math.min(num, 10)
            for (let i = 0; i < patternCount; i++) {
                if (i > 10) break
                pattern.push(20)
                pattern.push(120)
            }
            this.$vibrate(pattern)

            void this.onDialed(num)
            this.sprites.find((s) => s.id === 'dialedLabel')!.dialed += num
        }
        track(w)
        this.sprites.push(w)

        const s = Sprite(this.fingerStop)
        this.sprites.push(s)

        const d = Sprite(this.dialedLabel)
        this.sprites.push(d)

        this.gameLoop.start()

        // Ensure accurate starting state
        getCanvas().addEventListener('mouseleave', (e) => { w.onUp(e) })
        this.setDialedLabel(this.player.dialed)
    },

    beforeUnmount() {
        // Stop tracking pointer events on dialer sprite
        const wheelIndex = this.sprites.findIndex((sprite) => sprite.id === 'fingerWheel')
        if (wheelIndex >= 0) {
            untrack(this.sprites[wheelIndex])
        }

        for (let i = 0; i < this.sprites.length; i++) {
            this.sprites[i].ttl = 0
        }

        this.gameLoop?.update(1 / 60)
        this.gameLoop?.stop()
    },

    methods: {
        setDialedLabel(dialed: string) {
            const label = this.sprites.find((s) => s.id === 'dialedLabel')

            if (label) {
                label.dialed = dialed

                switch (this.player.status) {
                case 'busy':
                    label.status = this.$t('DIALING.BUSY')
                    break
                case 'connected':
                    label.status = this.$t('DIALING.CONNECTED')
                    break
                case 'wrongnumber':
                    label.status = this.$t('DIALING.WRONG_NUMBER')
                    break
                default:
                    label.status = this.player.status
                }
            }
        },

        async onDialed(num: number) {
            try {
                await this.$ecast.updateObject<TriviaDeath2.Dialing.Response>(
                    this.player.responseKey,
                    {
                        action: 'dial',
                        num
                    }
                )
            } catch (error) {
                this.$handleEcastError(error)
            }
        },

        async onReset() {
            try {
                await this.$ecast.updateObject<TriviaDeath2.Dialing.Response>(
                    this.player.responseKey,
                    { action: 'hangup' }
                )
            } catch (error) {
                this.$handleEcastError(error)
            }
        }
    }
})
</script>

<style lang="scss" scoped>
.dialing {
    display: flex;
    flex-direction: column;
    align-items: center;

    canvas {
        width: clamp(150px, min(100%, 60vh), 640px);
    }

    .reset {
        display: block;
        padding: 20px 50px;
        border: none;
        border-radius: 15px;
        background-color: black;
        color: red !important;
        font-family: 'Lost and Forelorn';
        font-size: 4rem;
        line-height: 3rem;
    }
}
</style>
